import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        api: 'https://mahasiswa.test/api/',
        auth: localStorage.getItem("auth") || false,
        user: JSON.parse(localStorage.getItem("user")) || '',
        access_token: localStorage.getItem("access_token") || '',
        errorMessages: '',
        leftMenu: '',
        showAuth: false,
        settings: false,
        theme: localStorage.getItem("theme") || 'theme-dark-blue',
        primaryColor: localStorage.getItem("primary-color") || 'primary-indigo',
        listMenu: [
            {
                name: 'Home',
                path: '/',
            },
            {
                name: 'Core',
                path: '',
                admin: true,
                sub: [
                    {
                        name: 'Users',
                        path: '/users'
                    }
                ]
            },
            {
                name: 'Mahasiswa',
                path: '',
                sub: [
                    {
                        name: 'StartUp',
                        path: '/startup'
                    },
                    {
                        name: 'Organization',
                        path: '/organization'
                    },
                     {
                        name: 'Forum',
                        path: '/forum'
                    },
                    {
                        name: 'Explore',
                        path: '/explore'
                    }
                ]
            },
            {
                name: 'Event',
                path: '',
                sub: [
                    {
                        name: 'New Event',
                        path: '/new/event'
                    },
                    {
                        name: 'Events',
                        path: '/event'
                    }
                ]
            },
            {
                name: 'Social action',
                path: '',
                sub: [
                    {
                        name: 'New Action',
                        path: '/new/action'
                    },
                    {
                        name: 'Social Actions',
                        path: '/action'
                    }
                ]
            }
        ]
    },
    mutations: {
        setErrorMessages(state,to){
            state.errorMessages = to
        },
        toggleLeftMenu(state, to) {
            state.leftMenu = to;
        },
        setTheme(state, to) {
            state.theme = to
            localStorage.setItem("theme", to)
        },
        setPrimaryColor(state,to){
            state.primaryColor = to
            localStorage.setItem("primary-color", to)
        },
        toggleSetting(state,to){
            if(state.settings){
                state.settings = false
            }else{
                state.settings = true
            }
        },
        toggleShowAuth(state,to){
            if(state.showAuth){
                state.showAuth = false
            }else{
                state.showAuth = true
            }
        },
        login(state,data){
        //    Login
            axios.post(state.api+'login',data)
                    .then(res => {

                        console.log(res.data)

                        if(res.data.user){
                            localStorage.setItem("auth",true)
                            localStorage.setItem("user",JSON.stringify(res.data.user))
                            localStorage.setItem("access_token",res.data.access_token)
                                
                            state.auth = true
                            state.user = res.data.user
                            state.access_token = res.data.access_token

                            state.showAuth = false

                            state.errorMessages = ''

                        }else{
                             state.errorMessages = res.data.message
                         }
                    })
                     .catch(err => {
                            console.log(err.response.data.message)
                            state.errorMessages = err.response.data.message
                    })
        },
         register(state,data){
        //    Register
                axios.post(state.api+'register',data)
                    .then(res => {
                        
                        console.log(res.data)

                         if(res.data.user){

                            localStorage.setItem("auth",true)
                            localStorage.setItem("user",JSON.stringify(res.data.user))
                            localStorage.setItem("access_token",res.data.access_token)
                            
                            state.auth = true
                            state.user = res.data.user
                            state.access_token = res.data.access_token

                            state.showAuth = false
                             state.errorMessages = ''
                         }else{
                             state.errorMessages = res.data.message
                         }

                    })
                    .catch(err => {
                            console.log(err.response.data.message)
                            state.errorMessages = err.response.data.message
                    })
                    
        },
         logout(state,to){
            localStorage.clear()
            location.href = '/'
        },
    },
    actions: {

    },
    modules: {}
})